import React from "react"
import { BsChatRightQuoteFill } from "react-icons/bs";
import BlogApi from "./BlogApi"
const Slide = ({ id, image, name, valueIndex, index }) => {
  let position = "nextSlide"
  if (valueIndex === index) {
    position = "activeSlide"
  }
  if (valueIndex === index - 1 || (index === 0 && valueIndex === BlogApi.length - 1)) {
    position = "lastSlide"
  }
  return (
    <>
      <article className={`d_flex ${position}`} key={id}>
        {/*<div className='box d_flex' className={position} key={id}>*/}
        <div className='left box_shodow'>
          <div className='img'>
            <img src={image} alt='' height="700" width="800"/>
          </div>
          <div className='details mtop'>
            
            <h2>{name}</h2>
          
          </div>
        </div>

        <div className='right'>
          <div className='icon'>
            <div className='quote'>
            <BsChatRightQuoteFill/>
            </div>
          </div>

        </div>
      </article>
    </>
  )
}

export default Slide
