import React, { useState, useEffect } from "react"
import "./Blog.css"
import Slide from "./Slide"
import BlogApi from "./BlogApi"
import {  BsChevronCompactRight,BsChevronCompactLeft } from "react-icons/bs";

const Blog = () => {
  const [data, setdata] = useState(BlogApi)
  const [index, setIndex] = useState(0)

  useEffect(() => {
    const lastIndex = data.length - 1
    if (index < 0) {
      setIndex(lastIndex)
    }
    if (index > lastIndex) {
      setIndex(0)
    }
  }, [index, data])

  useEffect(() => {
    let slider = setInterval(() => {
      setIndex(index + 1)
    }, 3000)
    return () => clearInterval(slider)
  }, [index])

  return (
    <>
      <section className='Testimonial card cardos bg-light' id='clients'>
        <div className='container'>
          <div className='heading text-center'>
            <h1>"Galerie"</h1>
            <h5><u>Quelques Photos Pendant La Formation</u>  </h5>
          </div>
          <div className='slide'>
          

            {data.map((value, valueIndex) => {
              return < Slide key={value.id} {...value} valueIndex={valueIndex} index={index} />
            })}

            <div className='slide_button'>
              <button className='btn_shadow prev_btn' onClick={() => setIndex(index - 1)}>
              <BsChevronCompactLeft/>
              </button>
              <button className='btn_shadow next_btn' onClick={() => setIndex(index + 1)}>
              <BsChevronCompactRight/>
              </button>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default Blog
